::-webkit-scrollbar {
    display: none;
}

.project-container{
    position: relative;
}

.project-title-display{
    position: absolute;
    top:0%;
    height: 100%;
    width: 100%;
    z-index: 1;
}

.project-title{
    position: absolute;
    width: 100%;
    top:50%;
    transform: translate(0%, -50%);;
    opacity: 0.85;
}

.project-title:hover{
    cursor: pointer;
    text-decoration: underline;
}

.project-badge{
    position: absolute;
    top: 3%;
    right: 12%;
    opacity: 1.0;
}
